@import url(https://db.onlinewebfonts.com/c/2a97ee8e9e3ab9853429d31762897e66?family=TrumpGothicEastW01-Bold);
@import url('https://fonts.googleapis.com/css?family=Lato:400,400i,700,700i|Titillium+Web:400,400i,600,600i,700,700i,900');
svg{
	display: inline-block;
	vertical-align: middle;
	@include ba-transition('fill, stroke');
}
a,
[type="submit"],
[type="button"],
button{
	@include ba-transition;
}
.ek-slider{
	top:50%;
	background-color: $white;
	counter-reset: number;
	margin-top: 10px;
	box-shadow: 0px 20px 50px 0px rgba(0, 0, 0, 0.4);
	@include breakpoint (medium){
	margin-top: 70px;
	}
	
	&__slide{
		position: relative;
		counter-increment: number;
		@include breakpoint (medium){
		display: flex;
		padding: 25px;
		}
		&:after{
			position: absolute;
			content: "0"counter(number);
			right: 0px;
			font-size: 60px;
			font-weight: 600;
			color: #f4f4f4;
			bottom: -25px;
			@include breakpoint (medium){
			bottom: -32px;
			font-size: 80px;			
			}
			@include breakpoint (large){
				bottom: -50px;
				font-size: 118px;	
			}
		}
	}
	&__slide-right-col{
		padding: 25px 15px;
		@include breakpoint (medium){
			padding: 0 0 0 25px;
		}
		@include breakpoint (large){
			padding-top: 27px;
			padding-left: 0;
		}
	}
	&__star{	
		img{
			margin: 0 auto;
			@include breakpoint(medium){
			margin-bottom: 25px;
			margin-left: 32px;
			}
		}
	}
	&__title{
		font-size: 38px;
		text-transform: uppercase;
		color: $primary-color;
		line-height: 0.99;
		@include breakpoint (medium){
			font-size: 45px;
			line-height: 0.99;
			letter-spacing: .005em;
			margin-bottom: 20px;
		}
		@include breakpoint (large){
			font-size: 106px;
			margin-left: -80px;
			
			}
	}
	&__desc{
		font-size: 14px;
		color: $dark-gray;
		max-width: 265px;
		@include breakpoint (medium down){
			margin-bottom: 5px;
		}
	}
	&__readmore-link{
		padding: 5px 0;
		color: $primary-color;
		text-transform: uppercase;
		font-size: 12px;
		letter-spacing: .3em;
		border-bottom: 1px solid transparent;
		&:hover{
			border-bottom: 1px solid $primary-color;
		}

	}
}
.slick-initialized .slick-slide{
	outline: none;
	@include breakpoint (medium){
		display: flex;
		
	}
}
.slick-dots{
	position: absolute;
	right: 15px;
	list-style: none;
	bottom: 120px;
	@include breakpoint (medium){
		top: 90px;
		right: 25px;
	}
	@include breakpoint (large){
		top:110px;
	}
	
	
	li{
		color:$dark-gray;
		@include breakpoint (medium){
		margin-bottom: 15px;
		}
		@include breakpoint (large){
		margin-bottom: 30px;		
		}
	}
	button{
		color: $dark-gray;
		&::before{
			content: "0";
		}
		&:hover{
			color: $primary-color;
		}
	}
}
.slick-active>button{
	color: $black;
	font-weight: 600;
}